.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



body {
    color: #576366;
    background: #33546d;
    margin: 0;
    font-family: 'Roboto', sans-serif;
}

#root {
    background: #ffffff;
    width: 95%;
    max-width: 780px;
    padding-bottom: 2em;
    margin: 0 auto 2em;
    border-radius: .5em;
    -webkit-box-shadow: 0 3px rgba(0,0,0, .1);
    box-shadow: 0 3px rgba(0,0,0, .1);
    overflow: hidden;
}

#contenidos{
    margin-top: 10px;
}

header {
    background: #edeff0;
    margin-bottom: 2em;
}
.header-Courses{
    margin-bottom: 5px;
}
.main-content {
    padding: 1em 2em;
}

li{
  list-style: none;
}
.content{
    padding: 1%;
    text-align: justify;
    margin: 2%;
    background: #edeff0;
    border-radius: 10px;
}

.menu{
 text-align: right;
 right: 0;
 height: 50px;
}

.menu a{
  display: inline-block;
  background: transparent;
  padding: 10px;
  border-right: 1px solid grey;
  text-decoration: none;
  font-size: 20px;
}

.menu a:hover{
  background-color: #2cb0b7;
  color:#252626;
 
}
a:hover{
  text-decoration: none;
   background-color: #2cb0b7;
}

h1, h2, ul {
    margin: 0;
    font-family: 'Germania One', cursive;
    padding: 0;
}
h1{
  text-align: center;
}
h2 {
    color: #404040;
    font-size: 1.6em;
    margin-bottom: 1.25em;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
    color: #3b80d1;
}

hr {
    border: 0;
    height: 0;
    margin: 2.3em 0 2em 0;
    border-top: 1px solid rgba(0, 0, 0, .1);
}

.home a, input, button {
    padding: .5em;
    font-size: .9em;
    margin-right: 10px;
    border: none;
    border-radius: 3px;
    border: solid 1px #ccc;
    outline: none;
}

button {
    color: white;
    background: #4188dc;
    padding-left: 1em;
    padding-right: 1em;
    cursor: pointer;
    border-color: rgba(0,0,0, .1);
}



.group:after {
    content: "";
    display: table;
    clear: both;
}

.course {
    margin-bottom: 1em;
}

/* IMAGES -------- */
.course-img {
    width: 70px;
}

.teacher-img {
    width: 100%;
    border-radius: .3em;
}

.not-found {
    text-align: center;
}

/* MAIN NAV -------- */
.main-nav a {
    font-size: .95em;
    color: #222;
    text-align: center;
    text-transform: uppercase;
    display: block;
    padding: 1em;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

.main-nav a:hover, .main-nav .active {
    color: #fff;
    background: #2cb0b7;
}

/* COURSE NAV -------- */
.course-nav li {
    display: inline-block;
}

.course-nav li + li {
    margin-left: .6em;
}

.course-nav {
    float: right;
}

.course-nav a {
    text-transform: uppercase;
    display: block;
    font-size: .85em;
    color: #7d7d7d;
    padding: .2em .8em;
    border-radius: .25em;
    background: #eaebeb;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

.course-nav a:hover, .course-nav a.active {
    color: #fff;
    background: #39add1;
}

/* ICONS -------- */
.icn-logo {
    background: #54606b;
    color: #fff;
    margin-right: auto;
    padding: 0 2em;
}

.icn-logo i {
    font-size: 2.45em;
    line-height: 0;
    margin-top: 30px;
}

.icn-error {
    font-size: 3.2em;
    margin-bottom: .25em;
}


@media (min-width: 0) and (max-width: 768px) {
    body {
        padding-top: .8em;
    }

    .home a {
        border-color: transparent;
    }

    .icn-logo {
        display: block;
        width: 100%;
        text-align: center;
        padding-bottom: 4px;
    }

    .main-nav a {
        border-top: 2px solid transparent;
        border-bottom: 2px solid transparent;
    }

    .main-nav a:hover {
        border-color: rgba(0,0,0, .1);
    }

    .courses h2 {
        margin-bottom: 0;
    }

    .course-nav {
        display: block;
        margin: 1.5em 0 3.2em;
    }

    .home a, input, button {
        display: block;
        width: 100%;
        margin-bottom: 1em;
    }
}

@media (min-width: 769px) {
    body {
        padding-top: 3em;
    }

    .home a {
        border-color: #2cb0b7;
    }

    .media img, .media > div, .teacher {
        float: left;
    }

    .media > div {
        width: 85.1%;
        margin-left: 1.5em;
    }

    .teacher {
        width: 48.73%;
        margin-bottom: 1.5em;
        border-radius: 10px;
        background: #edeff0;
    }

    .teacher + .teacher {
        margin-left: 2.54%;
    }

    .teacher:nth-child(2n+3) {
        clear: left;
        margin-left: 0;
    }

    .teacher p{
        text-align: justify;

    }
    /* MAIN NAV -------- */
    .icn-logo {
        margin-right: auto;
        padding: 0 2em;
        line-height: 3;
    }

    header, .main-nav {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    }

    .main-nav a {
        border-left: 3px solid transparent;
        border-right: 3px solid transparent;
        padding: 1.25em 1.5em;
    }



    .main-nav a:hover {
        border-color: rgba(0,0,0, .1);
    }

    /* COURSE NAV -------- */
    .course-nav {
        float: right;
    }

    .courses h2 {
        float: left;
    }
}

/* fallback */
@font-face {
  font-family: 'Material Icons';
  font-style: normal;
  font-weight: 400;
  src: url(https://fonts.gstatic.com/s/materialicons/v29/2fcrYFNaTjcS6g4U3t-Y5ZjZjT5FdEJ140U2DJYC3mY.woff2) format('woff2');
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  -webkit-font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
}


